/**
 * External dependencies
 */
import { addFilter } from '@wordpress/hooks';
import {__} from '@wordpress/i18n';

/**
 * Add "Average order weight" total summary to WooCommerce Analytics -> Orders
 */

addFilter(
	'woocommerce_admin_orders_report_charts',
	'woo_order_weight_add_average_order_weight',
	(charts) => {
			charts = [...charts,
				{
					key: 'avg_order_weight',
					label: __( 'Average orer weight, kg', 'woocommerce' ),
					type: 'number'
				}];
		return charts;
	}
);

/**
 * Add "weight" to WooCommerce Analytics -> Orders
 */

 const addTableColumn = reportTableData => {
     if ('orders' !== reportTableData.endpoint) {
         return reportTableData;
     }

     const newHeaders = [
         ...reportTableData.headers,

         {
             label: __( 'Weight', 'woocommerce' ),
             key: 'order_weight',
             required: false,

         },
     ];

     const newRows = reportTableData.rows.map((row, index) => {
         const item = reportTableData.items.data[index];


				 const formatted_order_weight = item.order_weight + ' ' + woo_order_weight.weight_unit;

         const newRow = [
             ...row,
             {
                 display: formatted_order_weight,
                 value: item.order_weight,
             },
         ];
         return newRow;

     });

     reportTableData.headers = newHeaders;
     reportTableData.rows = newRows;

     return reportTableData;
 };

 addFilter('woocommerce_admin_report_table', 'woo_order_weight_add_order_weight', addTableColumn);
